import React, { Component } from 'react';
import "../style.css";
import { Checkbox } from 'semantic-ui-react';

class MainToDo extends Component {
    constructor(props) {
    super(props);

    this.state = {
      userInput: '',
      list: [],
      clickedItem: [],
      taskList: '',
      projectItems: [],
      addedNewTasks: [],
      on: false,
      temporaryStorage: [],
      nullState: [],
      itemForRemoving: '',
      clickedTask: []
    }
  this.handleChangeRemove = this.handleChangeRemove.bind(this);
  }
  firstUserInput = (input) => {
    this.setState({
        userInput: input
    });
  }
  
  addingTask = (input) => {
      this.setState({
          taskList: input
      })
  }

  addToList = (input) => {
      let listArray = this.state.list;
      listArray.push(input);

      localStorage.setItem('listLS', JSON.stringify(this.state.list));
    
      this.setState({
          list: listArray,
          userInput: '',
          addedNewTasks: []
      })
  }
  
  addingTaskToList = (input) => {
    let x = localStorage.getItem('clickedItem');
    let y = x;
    let alpha = this.state.taskList;

    let a = localStorage.getItem('clickedItem');
    let b = localStorage.getItem(a);

    localStorage.setItem('shadowedTasks', b);
    localStorage.setItem(y, JSON.stringify(alpha));
    let c = localStorage.getItem('shadowedTasks');

    let addTaskArray = this.state.addedNewTasks;
    addTaskArray.push(input); 
     
     this.setState({
        addedNewTasks: addTaskArray, 
         taskList: ''
     }) 

    localStorage.setItem(x, JSON.stringify(this.state.addedNewTasks.
        filter(function (value, index, arr) {
            return value !== "null"})
            )); 
}

removingInput = (input) => {
    this.setState({
        itemForRemoving: input
    })
}

   componentDidMount() {
       if(localStorage.getItem('listLS') === null) { 
                this.setState({
                    list: []
                })
            } else {
                let arrLS = localStorage.getItem('listLS');
                let parse = JSON.parse(arrLS);

                this.setState({
                      list: parse
                })
        }

        if(localStorage.getItem('clickedItem') === null) {
            this.setState({
                addedNewTasks: []
            })
         } else {
                let getClickedItemName = localStorage.getItem('clickedItem');
                let getTasks = localStorage.getItem(getClickedItemName); 
                let parseTasks = JSON.parse(getTasks);

                this.setState({
                    addedNewTasks: parseTasks
                });

                localStorage.setItem('shadowedTasks', getTasks); 
        } 
    } 
    
    componentDidUpdate() {
        if(localStorage.getItem('shadowedTasks') !== null && localStorage.getItem('clickedItem') !== null) {
        let a = localStorage.getItem('clickedItem');
        let b = localStorage.getItem(a);
        let c = localStorage.getItem('shadowedTasks');

                let getTasks = localStorage.getItem([a]);
                let combine = [getTasks||[], c||[]];
                const stateNull = combine.filter(function (value, index, arr) {
                         return value !== "null"
                 });
                    
                     let jointArray = [];

                     stateNull.forEach(array => {
                        jointArray = [...jointArray, array] 
                     })
                     const stateNullFull = [...new Set([...jointArray])];
                     const [first, ...rest] = stateNullFull;
                     
                localStorage.setItem(a, first);
                localStorage.removeItem('shadowedTasks'); 
                
                this.setState({
                         addedNewTasks: JSON.parse(b)
                    })
        }
        let clickedRemovedItem = localStorage.getItem('clickedItem');
        localStorage.setItem(clickedRemovedItem, JSON.stringify(this.state.addedNewTasks))
        
} 

  componentWillUnmount() {
      window.addEventListener('beforeunload', function(e) {
          return localStorage.push(this.state.list);
      })
      window.addEventListener('onload', function() {
            return this.setState({
                addedNewTasks: []
            })
      })
  }
  
  handleClick = e => { //if clicked item is not state value
      let clicked = e.target.id; 
      let listLS = localStorage.getItem('listLS');
      let listLSParse = JSON.parse(listLS);
      localStorage.setItem('clickedItem', listLSParse[clicked]);
      let a = localStorage.getItem('clickedItem');
      let b = localStorage.getItem(a);

      this.setState({
        clickedItem: localStorage.getItem('clickedItem')
      });
      (b !== null) ? 
        this.setState({ 
            addedNewTasks: JSON.parse(b)
        }) :
        this.setState({addedNewTasks: ["null"]});
  }

  handleChangeRemove = e => {
      let a = localStorage.getItem('listLS');
      let b = JSON.parse(a);
      let c = b.filter(item => item !== e);

      localStorage.setItem('listLS', JSON.stringify(c));
      this.setState({
          itemForRemoving: ''
      })
      localStorage.removeItem(e);
      let d = this.state.list.filter(item => item !== e)
      this.setState({
          list: d
      })
    }

    removeTask = (clickedValue) => {
        console.log(this.state.addedNewTasks);
        console.log(clickedValue);
        let stateArr = this.state.addedNewTasks;
        let filterArr = stateArr.filter(item => item !== clickedValue);
            this.setState({
                addedNewTasks: filterArr
            })
    }
    render() {
        
        return (
                <div className="ui container">
                    <div className="ui horizontal segments">
                        <div className="ui left floated segment">
                            <h2 className="ui basic segment">Add project</h2>
                        <div className="ui action input">
                        <input className="margin" type="text" 
                            onChange={(e) => this.firstUserInput(e.target.value)}
                            value={this.state.userInput}  
                            placeholder="Enter Project" />
                        <button className="ui primary button" onClick={() => this.addToList(this.state.userInput)}>Add</button>
                        </div>
                        </div>
                        <div className="ui right floated segment">
                            <h2 className="ui basic segment">Remove project</h2>
                            <div className="ui action input">
                            <input className="margin floatright" type="text"
                                onChange={(e) => this.removingInput(e.target.value)}
                                value={this.state.itemForRemoving}
                                placeholder="Enter project for deleting" />
                            <button className="ui negative button" onClick={() => this.handleChangeRemove(this.state.itemForRemoving)}>Remove</button>
                        </div>
                        </div>
                    </div>
                    <div className="ui segment">
                        <h2 className="ui center aligned icon header">
                        <i className="clipboard list icon"></i>
                            Projects
                        </h2>
                        <div className="ui center aligned segment">
                            <ul style={{textDecoration: 'none', listStyleType: 'none', display: 'inline-block', marginLeft: "-33px"}}>
                                {this.state.list.map((obj, id) => 
                                    <a href="#task-segment" style={{color: 'inherit'}}><li key={obj[id]} id={id}
                                    onClick={this.handleClick}
                                    style={{cursor: 'pointer', fontSize: '20px', paddingBottom: '5px'}}>
                                        {obj}
                                    </li></a>
                                )} 
                            </ul>
                        </div>
                    </div>
                    <div id="task-segment" className="ui horizontal segments">
                        <div className="ui center aligned segment">
                        <h2 className="ui center aligned icon header">
                            {this.state.clickedItem} 
                        </h2>
                        <div className="ui action input">
                            <input  type="text"
                                    onChange={(e) => this.addingTask(e.target.value)}
                                    value={this.state.taskList} 
                            placeholder="Enter Task" /> 
                            <button className="ui button" onClick={() => this.addingTaskToList(this.state.taskList)}>Add Task</button> 
                        </div>
                    </div>
                    </div>
                    <div className="middle aligned divided list">
                        <div className="item">
                            
                            <div className="content">
                                
                                {this.state.addedNewTasks !== null && <ul style={{textDecoration: 'none', listStyleType:'none'}}>
                                    {this.state.addedNewTasks
                                        .filter(function (value, index, arr) {
                                            return value !== "null"})
                                        .map((obj, id) => 
                                    <li style={{ paddingBottom: '17px'}} key={obj[id]} id={id}>
                                        <label><input type="checkbox" id={obj[id]} style={{marginLeft: '-10%'}} onClick={() => {
                                            let checkedIS = document.getElementById(obj[id]);
                                            let isChecked = checkedIS.checked; //true or false
                                            if(isChecked === true) {
                                                let getClicked = localStorage.getItem('clickedItem'); //Simone
                                                localStorage.setItem(getClicked + 'Clicked', (obj)); //set simOne value 
                                                let x = localStorage.getItem(getClicked + 'Clicked'); // get simOne
                                             /*   let y = this.state.clickedItem;
                                                let z = y.concat(x);
                                                this.setState({
                                                    clickedItem: z
                                                })
                                                console.log(this.state.clickedItem); 


                                                let getCurrent = localStorage.getItem(x);
                                                
                                                let concated = [getCurrent, obj]
                                                
                                                console.log(concated); */
                                            } 
                                            }} />{obj}</label> 
                                        <button 
                                        onClick={() => this.removeTask(obj)} 
                                        className="ui right floated button">
                                            remove
                                        </button> 
                                    </li>
                                    )}                 
                                </ul>}
                            </div>
                        </div>
                    </div>
                </div>
        );
    }
}

export default MainToDo;